.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn:hover {
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background: white;
    box-shadow: 0 0 0.8em white;
    color: #211717;
    transition: 250ms;
}

.btn--primary {
    background-color: var(--highlight-color);
}

.btn--outline {
    background-color: transparent;
    color: white;
    padding: 8px 20px;
    border-radius: 4px;
    border: 1px solid var(--highlight-color);
    transition: all 0.3s ease-out;
}

.btn--medium {
    background-color: var(--highlight-color);
    padding: 8px 20px;
    border-radius: 4px;
    font-size: 18px;
    color: white;
}

.btn--large {
    padding: 12px 26px;
    border-radius: 4px;
    font-size: 20px;
    color: white;
    
}